.hero-main {
    height: 100vh;

    .hero-image {
        width: 100%;
    }

    .hero-text {
        @include montserrat(18px, 700);
    }
}

.main-section-maincontainer {
    background-color: $white;

    .section-one {
        &__image {
            display: block;
            width: 100%;
        }

        &__heading {
            color: $dark-main;
            @include montserrat(40px, 700);
            margin-bottom: 32px;

            @media (max-width: $xl) {
                font-size: 30px;
            }
        }

        &__desc {
            color: $cafe-noir;
            @include montserrat(18px, 700);
            margin-bottom: 48px;

            @media (max-width: $xl) {
                font-size: 16px;
            }
        }

        &__quantity {
            color: $cafe-noir;
            text-shadow: 4px 4px 30px rgba(0, 0, 0, 0.53);

            @include montserrat(48px, 700);
            @media (max-width: $xl) {
                font-size: 30px;
            }
        }

        &__label {
            color: $dark-main;
            @include montserrat(18px, 500);

            @media (max-width: $xl) {
                font-size: 15px;
            }
        }
    }

    .section-two {
        &__desc {
            @include montserrat(18px, 500);
        }

        .st-card {
            &__container {
                background-color: $dust-storm;
                border-radius: 18px;
                width: 95%;
                margin: auto;
                padding: 37px 24px;
                // box-shadow: 16px 16px 0px 6px rgba(0, 0, 0, 1);
                box-shadow: 20px 18px 0px -9px rgba(0, 0, 0, 1);
            }

            &__image {
                height: 83px;
                width: 83px;
                background-color: $white;
                border-radius: 8px;
                margin-bottom: 21px;

                .img {
                    height: 70%;
                }
            }

            &__name {
                @include montserrat(18px, 700);
                color: $dark-main;
            }

            &__label {
                @include montserrat(16px, 500);
                color: $cafe-noir;
                line-height: 130%;
            }
        }
    }

    .section-three {
        padding-bottom: 100px;
        overflow: hidden;

        .slick-dots {
            bottom: -50px;
        }
    }
}

.contact-main-container {
    background-color: $white;

    .input-email {
        background-color: $dust-storm;
        @include montserrat(16px, 500);
        padding: 10px 20px;
        outline: none;
        border: none;
        border-radius: 8px;
        width: 50%;

        @media (max-width: $md) {
            width: 100%;
        }

        &::placeholder {
            color: $black;
        }
    }
}

.about-container {
    overflow: hidden;

    .slick-track {
        display: flex !important;
    }

    .slick-slide {
        height: inherit !important;

        > div {
            height: 100%;
            .TS-card {
                height: 100%;
            }
        }
    }
}

.footer-main {
    background-color: $dust-storm;
}
